<template>
  <div>
    <el-drawer
      title="Mapeie os Campos do Novo Registro"
      :visible.sync="show"
      direction="rtl"
      :before-close="handleClose"
      size="50%"
    >
      <div style="padding: 0px 15px;">
        <p>
          Objeto de Origem:
          <br />
          <el-select v-model="form.newDataObject_Source" @change="searchFields('source')">
            <el-option
              v-for="item in form.objects"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </p>
        <p>
          Objeto de Conversão:
          <br />
          <el-select v-model="form.newDataObject_Conversion" @change="searchFields('conversion')">
            <el-option
              v-for="item in form.objects"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </p>

        <!-- <div v-if="showMapList()">
          <el-row v-for="(field, index) in this.form.source_fields" :key="field.field_id">
            <el-col :span="10">
              <p>{{ field.field_label }}</p>
            </el-col>
            <el-col :span="2">
              <span class="el-icon-right" style="margin-top: 20px"></span>
            </el-col>
            <el-col :span="10">
              <p>
                <el-select
                  v-model="source_fields[index].field_map"
                  placeholder="Selecione um campo"
                >
                  <el-option
                    v-for="c_field in this.form.newDataObject_Conversion"
                    :key="c_field.field_id"
                    :label="c_field.field_label"
                    :value="c_field.field_id"
                  ></el-option>
                </el-select>
              </p>
            </el-col>
          </el-row>
        </div>-->
      </div>
    </el-drawer>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        newDataObject_Source: "",
        newDataObject_Conversion: "",
        objects: [
          {
            value: "leads",
            label: "Leads"
          },
          {
            value: "accounts",
            label: "Contas"
          },
          {
            value: "contacts",
            label: "Contatos"
          },
          {
            value: "deals",
            label: "Oportunidaes"
          }
        ],
        source_fields: [],
        conversion_fields: []
      }
    };
  },
  props: ["show"],
  methods: {
    showMapList() {
      if (
        this.drawerFields.form.newDataObject_Source != "" &&
        this.drawerFields.form.newDataObject_Conversion != ""
      ) {
        return true;
      } else {
        return false;
      }
    },
    handleClose(done) {
      this.$confirm("Você tem certeza que deseja fechar a ediçao de campos?")
        .then(d => {
          this.$emit("done");
          done(d);
        })
        .catch({});
    },
    searchFields(origin) {
      var obj = null;
      if (origin == "source") {
        obj = this.drawerFields.form.newDataObject_Source;
      } else if (origin == "conversion") {
        obj = this.drawerFields.form.newDataObject_Conversion;
      }

      this.$http.get("/fields/" + obj + "?origin=" + origin).then(res => {
        if (origin == "source") {
          this.drawerFields.form.source_fields = res.data;
        } else if (origin == "conversion") {
          this.drawerFields.form.conversion_fields = res.data;
        }
      });
    }
  }
};
</script>

