<template>
  <div class="component__content">
    <el-row>
      <el-col :xs="24" :sm="24" :md="20" :lg="20" :xl="20">
        <h1 class="pageTitle">{{ this.pageTitle }} > {{ this.workflowData['name'] }}</h1>
      </el-col>
      <el-col :xs="24" :sm="24" :md="4" :lg="4" :xl="4" class="text-right">
        <el-button plain @click="returnWorkflows()">Voltar a Workflows</el-button>
      </el-col>

      <el-col id="listData" :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
        <el-row>
          <h2 class="pageTitle">Crie as ações a serem executadas quando o workflow for acionado</h2>

          <el-table :data="actions" style="width: 100%">
            <el-table-column width="50" type="index"></el-table-column>
            <el-table-column prop="type">
              <template slot-scope="scope">
                <el-select
                  v-model="actions[scope.$index]['type']"
                  @change="actionByValue(actions[scope.$index]['type'])"
                  placeholder="Selecione a Ação"
                >
                  <el-option
                    v-for="item in selectOptionsType"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </template>
            </el-table-column>
            <el-table-column width="50">
              <template slot-scope="scope">
                <el-button
                  type="danger"
                  @click="removeAction(scope.$index)"
                  icon="el-icon-minus"
                  circle
                  size="mini"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>

          <el-button type="text" icon="el-icon-plus" @click="addAction()">Nova ação</el-button>
        </el-row>
      </el-col>
    </el-row>

    <MapFields :show="showMapingFields" @done="doneMapingFields()" />
  </div>
</template>

<script>
import MapFields from "@/views/restricted/Settings/Workflows/MapFieldsDrawer";

export default {
  name: "WorkflowView",
  components: { MapFields },
  data() {
    return {
      showMapingFields: false,
      pageTitle: "Fluxo de Trabalho ",
      workflowData: [],
      actions: [],
      select: "",
      selectOptionsType: [
        {
          label: "Criar registro",
          value: "create-data"
        }
      ]
    };
  },
  head: {
    title: {
      inner: "Configurações | Fluxos de Trabalho",
      complement: "GuiaSales"
    }
  },
  methods: {
    doneMapingFields() {
      this.showMapingFields = false;
    },
    actionByValue(type) {
      switch (type) {
        case "create-data":
          this.showMapingFields = true;
          break;

        default:
          break;
      }
    },
    removeAction(index) {
      this.actions.splice(index, 1);
    },
    addAction() {
      var obj = this.actions;
      var element = {};
      element.type = "";
      obj.push(element);
    },
    returnWorkflows() {
      this.$router.push({
        name: "Settings_Custom_Workflows"
      });
    },
    getWorkflow(id) {
      this.$http.get("/workflows/" + id).then(res => {
        if (res.status == "200") {
          this.workflowData = res.data;
        }
      });
    }
  },
  mounted() {
    this.getWorkflow(this.$route.params.id);
  }
};
</script>

<style lang="scss">
.drawer-content {
  padding: 0px 20px;
}

.content {
  min-height: 40vh;
  text-align: center;
}

.component__content {
  #listData {
    background-color: white;
    padding: 15px;
    margin-top: 20px;
  }
}
</style>